import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
  CardFooter,
  CardDescription,
} from "../components/ui/card";
import { Button } from "../components/ui/button";
import {
  Users,
  FolderTree,
  Tags,
  Bell,
  Grid,
  Workflow,
  Book,
  Settings,
  BarChart3,
  Layers,
} from "lucide-react";
import { useAuth } from "../context/AuthContext";

const AdminDashboardPage = () => {
  const { user } = useAuth();

  const adminModules = [
    {
      title: "Qun l danh mc",
      icon: <FolderTree className="h-8 w-8 mb-2 text-primary" />,
      description: "Thm, sa, xa cc danh mc trong h thng",
      link: "/admin/categories",
      color: "bg-gradient-to-br from-purple-500/10 to-purple-700/10",
      border: "border-purple-500/20",
    },
    {
      title: "Qun l ngi dng",
      icon: <Users className="h-8 w-8 mb-2 text-primary" />,
      description: "Qun l ti khon ngi dng, phn quyn admin",
      link: "/admin/users",
      color: "bg-gradient-to-br from-blue-500/10 to-blue-700/10",
      border: "border-blue-500/20",
    },
    {
      title: "Thng bo h thng",
      icon: <Bell className="h-8 w-8 mb-2 text-primary" />,
      description: "Gi v qun l thng bo cho ton b h thng",
      link: "/admin/notifications",
      color: "bg-gradient-to-br from-yellow-500/10 to-yellow-700/10",
      border: "border-yellow-500/20",
    },
    {
      title: "Qun l k nng",
      icon: <Layers className="h-8 w-8 mb-2 text-primary" />,
      description: "Thm, sa, xa cc k nng trong h thng",
      link: "/admin/skills",
      color: "bg-gradient-to-br from-green-500/10 to-green-700/10",
      border: "border-green-500/20",
    },
    {
      title: "Qun l th tag",
      icon: <Tags className="h-8 w-8 mb-2 text-primary" />,
      description: "Thm, sa, xa cc th tag cho l trnh",
      link: "/admin/tags",
      color: "bg-gradient-to-br from-pink-500/10 to-pink-700/10",
      border: "border-pink-500/20",
    },
    {
      title: "Qun l kha hc",
      icon: <Book className="h-8 w-8 mb-2 text-primary" />,
      description: "Thm, sa, xa kha hc trong h thng",
      link: "/admin/courses",
      color: "bg-gradient-to-br from-cyan-500/10 to-cyan-700/10",
      border: "border-cyan-500/20",
    },
  ];

  return (
    <div className="container mx-auto py-8">
      <div className="flex flex-col md:flex-row md:justify-between items-start md:items-center mb-6">
        <div>
          <h1 className="text-2xl font-semibold text-foreground">
            Qun tr h thng
          </h1>
          <p className="text-muted-foreground mt-1">
            Cho mng, {user?.username || "Admin"}! Qun l ton b h thng t
            y.
          </p>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
        {adminModules.map((module, index) => (
          <Link to={module.link} key={index} className="no-underline">
            <Card
              className={`h-full transition-all duration-300 hover:shadow-md ${module.color} border ${module.border} hover:-translate-y-1`}
            >
              <CardHeader>
                <div className="flex flex-col items-center mb-2">
                  {module.icon}
                  <CardTitle className="text-xl font-medium text-center">
                    {module.title}
                  </CardTitle>
                </div>
                <CardDescription className="text-center text-muted-foreground">
                  {module.description}
                </CardDescription>
              </CardHeader>
            </Card>
          </Link>
        ))}
      </div>

      <div className="mt-12 mb-8">
        <h2 className="text-xl font-medium mb-4">S liu tng quan</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          <Card className="bg-gradient-to-br from-purple-500/10 to-purple-700/10 border-purple-500/20">
            <CardHeader className="pb-2">
              <CardTitle className="text-md font-medium">Ngi dng</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex justify-between items-center">
                <span className="text-3xl font-bold">--</span>
                <Users className="h-6 w-6 text-primary" />
              </div>
            </CardContent>
          </Card>

          <Card className="bg-gradient-to-br from-blue-500/10 to-blue-700/10 border-blue-500/20">
            <CardHeader className="pb-2">
              <CardTitle className="text-md font-medium">L trnh</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex justify-between items-center">
                <span className="text-3xl font-bold">--</span>
                <Workflow className="h-6 w-6 text-primary" />
              </div>
            </CardContent>
          </Card>

          <Card className="bg-gradient-to-br from-green-500/10 to-green-700/10 border-green-500/20">
            <CardHeader className="pb-2">
              <CardTitle className="text-md font-medium">Kha hc</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex justify-between items-center">
                <span className="text-3xl font-bold">--</span>
                <Book className="h-6 w-6 text-primary" />
              </div>
            </CardContent>
          </Card>

          <Card className="bg-gradient-to-br from-yellow-500/10 to-yellow-700/10 border-yellow-500/20">
            <CardHeader className="pb-2">
              <CardTitle className="text-md font-medium">Danh mc</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex justify-between items-center">
                <span className="text-3xl font-bold">--</span>
                <FolderTree className="h-6 w-6 text-primary" />
              </div>
            </CardContent>
          </Card>
        </div>
      </div>

      <div className="mt-6 mb-4">
        <h2 className="text-xl font-medium mb-4">Truy cp nhanh</h2>
        <div className="flex flex-wrap gap-4">
          <Button
            variant="outline"
            className="border-primary/20 hover:border-primary/40"
            asChild
          >
            <Link to="/admin/categories">
              <FolderTree className="h-4 w-4 mr-2" /> Qun l danh mc
            </Link>
          </Button>
          <Button
            variant="outline"
            className="border-primary/20 hover:border-primary/40"
            asChild
          >
            <Link to="/admin/users">
              <Users className="h-4 w-4 mr-2" /> Qun l ngi dng
            </Link>
          </Button>
          <Button
            variant="outline"
            className="border-primary/20 hover:border-primary/40"
            asChild
          >
            <Link to="/admin/notifications">
              <Bell className="h-4 w-4 mr-2" /> Gi thng bo h thng
            </Link>
          </Button>
        </div>
      </div>
    </div>
  );
};

export default AdminDashboardPage;
