import React, { useState } from "react";
import { Bell } from "lucide-react";
import { Button } from "./ui/button";
import {
  DropdownMenu,
  DropdownMenuTrigger,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuLabel,
} from "./ui/dropdown-menu.jsx";

export function NotificationDropdown() {
  const [hasUnreadNotifications, setHasUnreadNotifications] = useState(true);

  // D liu thng bo mu
  const notifications = [
    {
      id: 1,
      title: " thm kha hc mi",
      message: " thm kha hc React nng cao vo l trnh Web.",
      time: "10 pht trc",
      read: false,
    },
    {
      id: 2,
      title: "Cp nht l trnh hc",
      message: "L trnh Khoa hc d liu bn ang theo di  c cp nht.",
      time: "1 gi trc",
      read: false,
    },
    {
      id: 3,
      title: "Nhc nh hon thnh kha hc",
      message: "Bn  hon thnh kha hc C bn Python.",
      time: "Hm qua",
      read: true,
    },
  ];

  const markAllAsRead = () => {
    setHasUnreadNotifications(false);
    // C th thm API call  cp nht trng thi thng bo trn my ch
  };

  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <Button variant="ghost" size="icon" className="relative">
          <Bell className="h-[1.2rem] w-[1.2rem]" />
          {hasUnreadNotifications && (
            <span className="absolute right-1 top-1 h-2 w-2 rounded-full bg-destructive" />
          )}
          <span className="sr-only">Xem thng bo</span>
        </Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent align="end" className="w-80">
        <DropdownMenuLabel className="flex justify-between">
          <span>Thng bo</span>
          <Button
            variant="ghost"
            size="sm"
            className="h-auto p-0 text-xs font-normal text-primary"
            onClick={markAllAsRead}
          >
            nh du  c tt c
          </Button>
        </DropdownMenuLabel>
        <DropdownMenuSeparator />
        {notifications.length > 0 ? (
          <>
            {notifications.map((notification) => (
              <DropdownMenuItem
                key={notification.id}
                className="flex flex-col items-start gap-1 p-4"
              >
                <div className="flex w-full justify-between">
                  <p className="font-medium">{notification.title}</p>
                  <span className="text-xs text-muted-foreground">
                    {notification.time}
                  </span>
                </div>
                <p className="text-sm text-muted-foreground">
                  {notification.message}
                </p>
                {!notification.read && (
                  <span className="mt-1 h-1.5 w-1.5 rounded-full bg-primary" />
                )}
              </DropdownMenuItem>
            ))}
          </>
        ) : (
          <div className="p-4 text-center text-muted-foreground">
            Khng c thng bo
          </div>
        )}
      </DropdownMenuContent>
    </DropdownMenu>
  );
}
