import axios from "axios";

// Base URL for API requests
const API_URL = process.env.REACT_APP_API_URL || "http://localhost:5000/api";

// 401
let isHandlingAuth = false;
// 401
let lastAuthErrorTimestamp = 0;

// Create axios instance
const api = axios.create({
  baseURL: API_URL,
  headers: {
    "Content-Type": "application/json",
  },
  // 
  timeout: 10000,
});

// Add a request interceptor to add the auth token to every request
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem("token");
    if (token) {
      // Use Authorization Bearer header (standard JWT auth)
      config.headers["Authorization"] = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Add a response interceptor to handle token expiration
api.interceptors.response.use(
  (response) => response,
  (error) => {
    // 
    if (error.response) {
      // 401
      if (error.response.status === 401) {
        const currentTime = Date.now();
        // 5401
        if (!isHandlingAuth && currentTime - lastAuthErrorTimestamp > 5000) {
          isHandlingAuth = true;
          lastAuthErrorTimestamp = currentTime;

          console.log(
            "Auth error:",
            error.response?.data?.code || ""
          );

          // 
          localStorage.removeItem("token");

          // 
          if (localStorage.getItem("devMode") === "true") {
            localStorage.removeItem("devModeUser");
          }

          // APIAuthorization
          delete api.defaults.headers.common["Authorization"];

          // 
          const currentPath = window.location.pathname;
          if (
            !currentPath.includes("/login") &&
            !currentPath.includes("/register")
          ) {
            // 
            console.error(
              "",
              error.response.data?.message || ""
            );

            // 
            setTimeout(() => {
              window.location.href = "/login";
              isHandlingAuth = false;
            }, 300);
          } else {
            isHandlingAuth = false;
          }
        }
      }

      // 
      else if (error.response.status >= 500) {
        console.error(":", error.response.data);
      }
    } else if (error.request) {
      // 
      console.error(": ", error.request);
    } else {
      // 
      console.error(":", error.message);
    }

    return Promise.reject(error);
  }
);

export default api;
