/**
 * X l li ResizeObserver loop trong ReactFlow
 * Li ny khng nh hng n chc nng hot ng ca ng dng
 * nhng gy ra cnh bo kh chu trong console
 */

export function applyResizeObserverFix() {
  if (typeof window === "undefined") {
    return; // Khng p dng trn server-side rendering
  }

  // Ghi  phng thc error ca console  lc cc cnh bo ResizeObserver
  const originalConsoleError = console.error;
  console.error = (...args) => {
    // Kim tra cc dng thng bo li khc nhau
    if (
      args[0] &&
      ((typeof args[0] === "string" &&
        args[0].includes("ResizeObserver loop")) ||
        args[0]?.message?.includes?.("ResizeObserver loop") ||
        args[0]?.toString?.().includes?.("ResizeObserver loop"))
    ) {
      // B qua li ResizeObserver loop
      return;
    }
    // Gi phng thc gc cho cc li khc
    originalConsoleError.apply(console, args);
  };

  // H tr c bit cho ReactFlow khi n s dng ResizeObserver
  if (window.ResizeObserver) {
    // Lu trin khai ResizeObserver gc
    const OriginalResizeObserver = window.ResizeObserver;

    // Ghi  vi phin bn x l li tt hn
    window.ResizeObserver = class EnhancedResizeObserver extends (
      OriginalResizeObserver
    ) {
      constructor(callback) {
        // Bc callback vi x l li
        const wrappedCallback = (entries, observer) => {
          // Debounce resize events to reduce frequency
          if (this._timeout) {
            clearTimeout(this._timeout);
          }

          this._timeout = setTimeout(() => {
            try {
              callback(entries, observer);
            } catch (error) {
              if (!String(error).includes("ResizeObserver loop")) {
                throw error; // Ch bt li ResizeObserver
              }
              // Otherwise silently ignore the error
            }
          }, 20); // Small delay to batch resize events
        };

        super(wrappedCallback);
        this._timeout = null;
      }

      disconnect() {
        if (this._timeout) {
          clearTimeout(this._timeout);
          this._timeout = null;
        }
        super.disconnect();
      }

      // Override observe to add additional error handling
      observe(target, options) {
        try {
          return super.observe(target, options);
        } catch (error) {
          console.warn("ResizeObserver error caught:", error);
          // Continue execution, don't break the app
        }
      }
    };
  }

  // Also handle error events globally
  window.addEventListener(
    "error",
    (event) => {
      if (event.message && event.message.includes("ResizeObserver loop")) {
        event.stopImmediatePropagation();
        event.preventDefault();
        return true;
      }
    },
    true
  );

  console.log(" Enhanced ResizeObserver fix applied");
}

export default applyResizeObserverFix;
